import { Fonts, Backgrounds } from '../components/colorMobile';
import { ColorToolbarConfig } from '../../utils/types';

export default function toolbar(colorConfig: ColorToolbarConfig) {
  const colorLocaleConfig = colorConfig.locale;
  return {
    buttons: {
      mobileFonts: Fonts,
      mobileBgs: Backgrounds,
    },
    locale: colorLocaleConfig,
  };
}
